import {Component, ElementRef, Input, OnInit, ViewChild} from '@angular/core';
import {Properties} from '../../properties/properties';
import {
    AlertController,
    IonInfiniteScroll,
    ModalController,
    NavController,
    ToastController
} from '@ionic/angular';
import {RestService} from '../../service/rest.service';
import {AuxiliaryList, InterventionByTheme, Sessions, themeSearch, ThemeSession} from '../../interfaces/model';
import {DatePipe} from '@angular/common';
import {ModalAttachmentPage} from '../modal/modal-attachment/modal-attachment.page';
import {ModalOrderAprovedPage} from '../modal/modal-order-aproved/modal-order-aproved.page';
import {ConfiguracionLista, NAME_LIST, NAME_LIST_SESSION_DELETE} from '../../reducers/favSessions/session.action';
import {AppState} from '../../app.reducers';
import {Store} from '@ngrx/store';
import {ModalInterveningPage} from '../modal/modal-intervening/modal-intervening.page';
import {NavigationExtras, Router} from '@angular/router';

@Component({
    selector: 'app-sesiones',
    templateUrl: './sesiones.page.html',
    styleUrls: ['./sesiones.page.scss'],
})
export class SesionesPage implements OnInit {

    changeSession: Sessions[] = [];
    sessionStorage: Sessions [] = [];
    sessionStar: boolean [] = [];
    j = 0;
    l = 0;
    searchSession: string = null;
    i = 0;
    hiddenThemes: boolean [] = [];
    dateFrom = null;
    dateTo = null;
    minDateFrom = new Date('2012-02-28').toISOString();
    maxDateFrom = new Date().toISOString();
    minDateTo = new Date('2012-02-28').toISOString();
    maxDateTo = new Date().toISOString();

    // @ts-ignore
    @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;
    constructor(public properties: Properties,
                private rest: RestService,
                private modalController: ModalController,
                private store: Store<AppState>,
                public toastController: ToastController,
                private alertController: AlertController,
                public navController: NavController,
                private router: Router) {
    }
    ngOnInit() {
    }
    ionViewWillEnter() {
        this.changeSession = [];
        this.sessionStar = [];
        this.i = 0;
        this.getSessionsList();
    }
    public loadData(event) {
            setTimeout(() => {
                this.getSessionsList(event);
            }, 500);
    }

    valFavorites(data: Sessions, index) {
        this.sessionStorage = JSON.parse(localStorage.getItem('session'));
        if (this.sessionStorage) {
            for (this.l = 0; this.l < this.sessionStorage.length; this.l++ ) {
                if ((this.sessionStorage[this.l].number === data.number) && (this.sessionStorage[this.l].startDate === data.startDate)) {
                    this.sessionStar[index] = true;
                    this.l = this.sessionStorage.length;
                } else {
                    this.sessionStar[index] = false;
                }
            }
        }
    }
    changeThemes() {
        const datePipe: DatePipe = new DatePipe('en-EN');
        let dateImport: string;
        this.changeSession.forEach(dataReco => {
            dateImport = datePipe.transform(dataReco['date'], 'yyyy-MM-dd');
            /*this.rest.getListThemesBySession({
                agendaId: dataReco['id'],
                date: dateImport
            }).subscribe(responseTheme => {
                dataReco['listThemes'] = responseTheme;
            });*/
        });
    }
    changeView(pos) {
        this.hiddenThemes[pos] = !this.hiddenThemes[pos];
    }

    getClear() {
        if (this.dateFrom !== null) {
            this.minDateTo = new Date(this.dateFrom).toISOString();
        } else {
            this.minDateTo = new Date('2012-02-28').toISOString();
        }
        if (this.dateTo !== null) {
            this.maxDateFrom = new Date(this.dateTo).toISOString();
        } else {
            this.maxDateFrom = new Date().toISOString();
        }
        this.i = 0;
        this.changeSession = [];
        this.getSessionsList();
    }

    async openAttachments(t: Sessions, i: ThemeSession) {
        const modal = await this.modalController.create({
            component: ModalAttachmentPage,
            componentProps: {
                "sessionId": t.id,
                "themeId": i.id
            }
        });

        modal.onDidDismiss().then((dataReturned) => {
            if (dataReturned !== null) {
            }
        });

        return await modal.present();
    }

    async openOrderApproved(session: Sessions) {
        const modal = await this.modalController.create({
            component: ModalOrderAprovedPage,
            componentProps: {
                "sessionObject": session
            }
        });

        modal.onDidDismiss().then((dataReturned) => {
            if (dataReturned !== null) {
                // this.dataReturned = dataReturned.data;
            }
        });

        return await modal.present();
    }

    putSession(item: Sessions, index) {
        let banValidate = false;
        this.sessionStorage = JSON.parse(localStorage.getItem('session'));
        if (this.sessionStorage) {
            for (this.j = 0; this.j < this.sessionStorage.length; this.j++) {
                if ((this.sessionStorage[this.j].number === item.number) && (this.sessionStorage[this.j].startDate === item.startDate)) {
                    // this.presentToast('La sesión ' + this.sessionStorage[this.j].number + ' ya se encuentra en favoritos')
                    this.removeSession(item, index);
                    banValidate = false;
                    return;
                } else {
                    banValidate = true;
                }
            }
        } else {
            banValidate = true;
        }
        if (banValidate === true) {
            this.sessionStar[index] = true;
            this.presentToast('Se añadio a favoritos la sesión seleccionada');
            const newLista = new ConfiguracionLista(item.id, item.number, item.startDate, item.tipo, item.date, item.list, item.initialDate, item.initialTime);
            this.store.dispatch({type: NAME_LIST, text: newLista});
        }
    }

    removeSession(item: Sessions, index) {
        const deleteList = new ConfiguracionLista(item.id,
            item.number, item.startDate, item.tipo, item.date, item.list, item.initialDate, item.initialTime);
        this.store.dispatch({type: NAME_LIST_SESSION_DELETE, text: deleteList});
        this.sessionStar[index] = false;
        this.presentToast('Sesión eliminda de la sección favoritos');
    }

    async presentToast(msg) {
        const toast = await this.toastController.create({
            message: msg,
            duration: 2000,
            position: 'bottom'
        });
        toast.present();
    }
    messageClose() {
        this.toastController.dismiss({
            'dismissed': true
        });
    }

    getIntervening(o: Sessions) {
        this.rest.getInterveningBySession({
            agendaId: o.id,
            date: o.startDate.substring(0, 10)
        }).subscribe(response => {
            let themeList: AuxiliaryList[] = [];
            let lst: InterventionByTheme[];
            lst = response;
            if (lst.length > 0) {
                const arr = lst.map(p => p.themeId);
                const s = new Set(arr);
                const u = Array.from(s);
                for( const n of u ){
                    let al: AuxiliaryList = {
                        description: '',
                        id: 0,
                        startdate: new Date(),
                        list: []
                    };
                    al.id = n;
                    for(const it of lst){
                        if( it.themeId === n ){
                            al.description = it.description;
                            break;
                        }
                    }
                    themeList.push(al);
                }
                this.openIntervening(lst, themeList);
            } else {
                this.presentToast( 'No existen intervenciones para la sesión');
            }
        });
    }
    async openIntervening(l: InterventionByTheme[], t: AuxiliaryList[]) {
        const modal = await this.modalController.create({
            component: ModalInterveningPage,
            componentProps: {
                "list": l,
                "themes": t
            }
        });
        return await modal.present();
    }


    resetFrom() {
        this.dateFrom = null;
        this.getClear();
    }

    resetTo() {
        this.dateTo = null;
        this.getClear();
    }

    resetText() {
        this.searchSession = null;
        this.getClear();
    }

    getSessionsList(event?) {
        this.infiniteScroll.disabled = false;
        let index = 0;
        this.hiddenThemes = [];
        const offset = 15;
        if (event !== undefined) {
            this.i = this.i + offset;
        }
        let t = '';
        let df = '';
        let dt = '';
        if ( this.searchSession != null ) {
            t = this.searchSession;
        }
        if ( this.dateFrom != null ) {
            df = this.dateFrom.toString().substr(0, 10);
        }
        if ( this.dateTo != null ) {
            dt = this.dateTo.toString().substr(0, 10);
        }
        let parts: string[];
        this.rest.getListSessions({
            sessionNumber: t,
            from: df,
            to: dt,
            offset: this.i,
        }).subscribe(response => {
            this.changeSession.push(...response);
            this.changeSession.forEach(data => {
                parts = data.startDate.split('[');
                data['date'] = new Date(parts[0]);
                this.valFavorites(data, index);
                this.hiddenThemes[index] = false;
                index++;
            });

            this.changeThemes();
            if (event) {
                event.target.complete();
            }
            if (response.length === 0 && event) {
                    event.target.disabled = true;
            }
        });
    }

    async navigateTo(a: AuxiliaryList, componente: string) {
        const navigationExtras: NavigationExtras = {
            queryParams: {
                id: a.id
            }
        };
        this.router.navigate([componente], navigationExtras);
    }

}
